.store-container {
  display: flex;
  justify-content: center;

  .item-container {
    max-width: 900px;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;

    .item-card {
      width: 200px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin: 5px;

      p {
        margin: 5px 0;
      }

      img {
        width: 200px;
      }

      .item-content {
        margin: 5px 0;
        display: flex;
        align-items: center;
        justify-content: space-around;

      }
    }
  }
}
